import React from 'react'
import { Link } from 'react-router-dom';

import ViewList from './viewList'
import axios from '../../API.js'

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      viewdata: []
    }
  }
  addnew = () => {
    this.props.history.push('/work/add');
  }
  deleteItem = (id) => {
    var r = window.confirm('Are u want to Delete?')
    if (r === true) {
      axios.post("/Works/Update", { id: id, action: 'del' }).then(res => {
        if (res.status === 'success')
          window.location.reload()
      })
    }
  }
  loadDate = () => {
    axios.get("/Works").then(res => {
      console.log(res)
      if (res.data.status === 'success')
        this.setState({ viewdata: res.data.data })

    })
  }
  componentDidMount = () => {
    this.loadDate();
  }
  render() {
    const data = {
      columns: [
        {
          label: 'ID',
          field: 'id',
          sort: 'asc',
        },
        {
          label: 'Requester',
          field: 'work',
          sort: 'asc',
        },
        {
          label: 'Entry Date',
          field: 'startdate',
          sort: 'asc',
        },
        {
          label: 'Expire Date',
          field: 'expiredate',
          sort: 'asc',
        },
        {
          label: 'File Name',
          field: 'filename',
          sort: 'asc',
        },
        {
          label: 'Action',
          field: 'action',
        }],
      rows: [...this.state.viewdata.map((res, i) => ({
        id: res.id,
        work: res.RequesterName,
        startdate: res.startDate,
        expiredate: res.expireDate,
        filename: res.fileName,
        action: <><Link className="btn btn-primary btn-sm" to={{ pathname: "/work/add", state: { id: res.id } }} onClick={this.toggle}>Edit</Link>&nbsp;<Link to={{}} className="btn btn-danger btn-sm" onClick={() => this.deleteItem(res.id)}>Delete</Link></>
      }))]
    }
    let view = (
      <>

        <div className="menubar">
          <ViewList header="Works" data={data} addnew={this.addnew} />
        </div>

      </>
    )
    return view;
  }
}
export default App;